import React from 'react'
import Divider from '@components/Divider'
import Figure from "@components/Mdx/Figure"
import Figcaption from "@components/Mdx/Figcaption"
import { Global, css } from '@emotion/react'


// Hook to detect when external script has been loaded
// https://usehooks.com/useScript/
function useScript(src) {
    // Keep track of script status ("idle", "loading", "ready", "error")
    const [status, setStatus] = React.useState(src ? "loading" : "idle");
    React.useEffect(
      () => {
        // Allow falsy src value if waiting on other data needed for
        // constructing the script URL passed to this hook.
        if (!src) {
          setStatus("idle");
          return;
        }
        // Fetch existing script element by src
        // It may have been added by another intance of this hook
        let script = document.querySelector(`script[src="${src}"]`);
        if (!script) {
          // Create script
          script = document.createElement("script");
          script.src = src;
          script.async = true;
          script.setAttribute("data-status", "loading");
          // Add script to document body
          document.body.appendChild(script);
          // Store status in attribute on script
          // This can be read by other instances of this hook
          const setAttributeFromEvent = (event) => {
            script.setAttribute(
              "data-status",
              event.type === "load" ? "ready" : "error"
            );
          };
          script.addEventListener("load", setAttributeFromEvent);
          script.addEventListener("error", setAttributeFromEvent);
        } else {
          // Grab existing script status from attribute and set to state.
          setStatus(script.getAttribute("data-status"));
        }
        // Script event handler to update status in state
        // Note: Even if the script already exists we still need to add
        // event handlers to update the state for *this* hook instance.
        const setStateFromEvent = (event) => {
          setStatus(event.type === "load" ? "ready" : "error");
        };
        // Add event listeners
        script.addEventListener("load", setStateFromEvent);
        script.addEventListener("error", setStateFromEvent);
        // Remove event listeners on cleanup
        return () => {
          if (script) {
            script.removeEventListener("load", setStateFromEvent);
            script.removeEventListener("error", setStateFromEvent);
          }
        };
      },
      [src] // Only re-run effect if script src changes
    );
    return status;
  }


const Bokeh = ({ plotJson, plotName, plotCaption, bokehVersion="2.2.2" }) => {
    // NB, the default version of bokeh here is due to:
    //   https://discourse.bokeh.org/t/problem-importing-bokeh-bokehjs-module-in-javascript-app/7510/9

    // need to wait until bokeh is loaded
    const status = useScript(`https://cdn.bokeh.org/bokeh/release/bokeh-${bokehVersion}.min.js`)
    // only call Bokeh.embed once
    const [embedded, setEmbed] = React.useState(false);
    // do a small thing
    React.useEffect (() => {
        if (status ==="ready" && !embedded) {
            window.Bokeh.embed.embed_item(plotJson, plotName)
            setEmbed(true)
        }
    }, [status, embedded, plotJson, plotName])
    // render a div in which bokeh puts the plot, wrap
    // that up to centre it in the Figure box. Also style
    // the root bokeh div a little differently to remove
    // nasty thin transparent elements in and around plots
    const divStyle = {
        display: "flex",
    }
    return (
        <>
        <Global
            styles={css`
                .bk-root {
                    margin: auto;
                    background: white;
                    border-radius: 7px;
                    padding: 7px;
                }
            `}
        />
        <Figure >
            <div class="wrap-{plotName}" style={divStyle}>
                <div id={plotName} className="bk-root"></div>
            </div>
            <Figcaption>{plotCaption}</Figcaption>
        </Figure>
        <Divider space={3} />
        </>
    )
}

const MathJax = (_) => {
    // need to wait until bokeh is loaded
    const status = useScript("https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.5/MathJax.js?config=TeX-AMS-MML_HTMLorMML")
    console.log("Hello from mathjax")
    // do a small thing
    React.useEffect (() => {
      if (status === "ready") {
        console.log("config mathjax")
        window.MathJax.Hub.Config({
          tex2jax: {
            inlineMath: [['$', '$'], ['\\(', '\\)']],
            displayMath: [['$$', '$$'], ['[', ']']],
            processEscapes: true,
            processEnvironments: true,
            skipTags: ['script', 'noscript', 'style', 'textarea', 'pre'],
            TeX: {
              equationNumbers: { autoNumber: 'AMS' },
              extensions: ['AMSmath.js', 'AMSsymbols.js'],
            },
          },
        })
        console.log(MathJax)
        if (window.MathJax != null) {
          window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub])
        }
      }
    }, [status])

  return (
    <></>
  )
}


export { Bokeh, MathJax }